.container {
  width: 80%;
  text-align: center;
  margin: 0 auto
}

.indicator {
  margin: 0 auto;
  position: absolute;
  right: 50%;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

@media(min-width:600px) {
  .tiles {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 2em;
  }

  .tile {
    width: calc(30% - 3em);
    padding: 1em;
    display: flex;
    flex-direction: column;
    position: relative;
    min-width: 300px;
    margin: 1em auto;
    text-align: left;
  }
}